#toast-container {
  position: fixed;
  width: 300px;
  z-index: 10002;
  right: 20px;
  bottom: 0;

  .toast {
    background-color: white;
    border-left: 5px solid !important;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.25),0 2px 10px 0 rgba(0,0,0,0.25);
    padding: 15px;
    margin-bottom: 20px;
    border-radius: 0;
    opacity: 1!important;

    &.toast-info {
      color: $dark-blue;
      border-color: $dark-blue;
    }

    &.toast-success {
      color: $dark-green;
      border-color: $dark-green;
    }

    &.toast-warning {
      color: $orange;
      border-color: $orange;
    }

    &.toast-error {
      color: $dark-red;
      border-color: $dark-red;
    }

    &:hover {
      box-shadow: 0 2px 5px 0 rgba(0,0,0,0.25),0 2px 10px 0 rgba(0,0,0,0.25)!important;
    }

    .btn-close {
      position: relative;
      top: -10px;
      right: -8px;
      color: inherit;
    }
  }
}
