
.table-summary {
  font-size: 1.2em;

  tr {
    td {
      border: 0;
      padding: 5px;
    }
  }

  .col-amount {
    text-align: right;

    input {
      text-align: right;
    }
  }

  .col-currency {
    text-align: left;
    padding-right: 0;
  }

  .row-added-costs {
    .col-title {
      position: relative;
      .close {
        float: none;
        font-size: 1em;
        position: absolute;
        top: -2px;
        margin-left: 5px;

        &:active, &:focus, &:hover {
          outline: 0;
        }
      }
    }
  }

  .row-inputs {
    input {
      font-size: 1em;
      background: transparent;
      border: 0;
      padding: 0;
      @include border-radius(0);
      height: 24px;
      color: $dark-grey;
    }

    .themed-select {
      width: 40px;

      button {
        @include border-radius(0);
        padding: 0;
        font-size: 1em;
        border: 0;

        span {
          color: $dark-grey;
        }

        .caret {
          right: 0;
          color: $dark-grey;
        }
      }

      .dropdown-menu {

        &.open {
          border: 1px solid $border-dark;
          right: 4px;
          left: initial;
          overflow: initial !important;
          @include box-shadow(0 2px 3px rgba(0, 0, 0, 0.3) !important);
          top: calc(100% + 10px);

          &:after {
            content: '';
            position: absolute;
            border-style: solid;
            border-width: 0 8px 8px;
            border-color: #FFFFFF transparent;
            display: block;
            width: 0;
            z-index: 1;
            top: -8px;
            left: 45px;
          }

          &:before {
            content: '';
            position: absolute;
            border-style: solid;
            border-width: 0 8px 8px;
            border-color: $border-dark transparent;
            display: block;
            width: 0;
            z-index: 0;
            top: -9px;
            left: 45px;
          }
        }

        &.inner {
          position: relative;
          width: 80px;
          border-bottom-left-radius: 3px;
          border-bottom-right-radius: 3px;
        }

        li {
          a {
            justify-content: flex-end;
          }
        }
      }
    }
  }

  .row-add-button {

    td {
      padding: 0 10px 15px 10px;
      position: relative;
      height: 44px;
    }

    .btn-add-cost {
      position: absolute;
      right: 0;
      top: 3px;
      margin: 0 3px;
      i {
        margin: 0 5px 0 0;
        font-size: 0.8em;
      }
    }
  }

  .row-total {
    td {
      padding: 10px 5px;
      border-top: 1px solid $border-dark;
      font-weight: bold;
      font-size: 1.6em;
    }
  }
}

.summary {
  margin: 15px 0 15px 0;
  border: 0;
}

